  font-display: swap;
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 400;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format("woff2");
}
.page-wrapper {
  display: grid;
  align-content: space-around;
  text-align: center;
  background-color: #0a2144;
  color: #cccccc;
  font-family: "Bebas Neue", system-ui;
  letter-spacing: 0.05rem;
}

.header {
  font-size: 2rem;
}

.main {
  display: grid;
  grid-template-areas: "text" "controls" "game";
  place-content: center;
  gap: 50px;
  grid-template-rows: min-content, min-content, 1fr;
}
.main__game-text {
  grid-area: text;
  font-size: 1.5rem;
}
.main__controls {
  grid-area: controls;
  display: grid;
  grid-auto-flow: column;
  place-content: space-around;
}
.main__controls .btn {
  appearance: none;
  font-size: 1.25rem;
  border-radius: 10px;
  padding: 0.5rem;
  background-color: #cccccc;
  color: #163b66;
}
.main__controls .btn:hover {
  background-color: #6196c6;
}
.main__game {
  grid-area: game;
  display: grid;
  grid-auto-flow: column;
  place-content: center;
  gap: 50px;
}
.main__board {
  height: 420px;
  width: 420px;
  border: 1px solid #163b66;
  display: grid;
  grid-template: repeat(10, 1fr)/repeat(10, 1fr);
}
.main__board__cell {
  background-color: #163b66;
  border: 1px solid #0a2144;
}
.main__board .ship {
  background-color: #c1cfda;
}
.main__board .hit {
  background-color: #941c2f;
}
.main__board .miss {
  background-color: #6196c6;
}
.main__board .valid-placement {
  background-color: #c1cfda;
}
.main__board .invalid-placement {
  background-color: #941c2f;
}

.footer {
  font-size: 1.125rem;
}
.footer__link {
  text-decoration: none;
  color: #6196c6;
}
.footer__link:hover {
  color: #cccccc;
}

.hidden {
  display: none;
